@font-face {
  font-family: "digital-7";
  src: url("digital-7.regular.ttf");
}
*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 10px;
}
@media only screen and (max-width: 600px) {
  html {
    font-size: 8px;
  }
}
@media only screen and (max-width: 480px) {
  html {
    font-size: 5.5px;
  }
}
/*************************************************/
figcaption,
h2 {
  font-family: Arial, Helvetica, sans-serif;
  color: rgba(0, 0, 0);
}
main {
  min-height: 100vh;
  padding: 4rem 0 0 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background-color: #282c34;
  color: white;
}
/************************************************/
.btn {
  /*display: inline-block;*/
  cursor: pointer;
  width: 100%;
  padding: 0.6rem 1.2rem;
  font-size: 1.6rem;
  font-weight: 700;
  line-height: 2rem;
  color: #ffffff;
  border-radius: 0.5rem;
  border-width: 0 0.1rem 0.2rem 0;
  border-color: rgba(0, 0, 0, 0.527);
  transition: 0.2s cubic-bezier(0.3, 0, 0.5, 1);
  transition-property: color, background-color, border-color;
}
.btn:hover {
  /*border-color: #1b1f2326;
  transition-duration: 0.1s;
  */
}
.btn-border {
  display: flex;
  border-radius: 0.5rem;
}
.btn-border-orange {
  margin: 0 0 1rem 0;
  background-color: rgba(171, 90, 32, 255);
  border: 0.2rem solid rgba(171, 90, 32, 255);
}
.btn-border-white {
  background-color: rgb(255, 255, 255);
  border: 0.2rem solid rgb(255, 255, 255);
}
.btn-dec,
.btn-inc {
  width: 100%;
  background-color: rgba(246, 235, 17);
}
.btn-dec:hover,
.btn-inc:hover {
  background-color: rgb(135, 135, 3);
}
.btn-dec {
  padding: 1.5rem 0.3rem 0 0.3rem;
}
.btn-inc {
  padding: 0 0.3rem 1.5rem 0.3rem;
}
.btn-idle {
  background-color: rgba(196, 3, 3);
}
.btn-idle:hover {
  background-color: rgba(154, 2, 2);
}
.btn-menu,
.btn-silence {
  margin: 1rem;
  padding: 1rem 0.2rem;
  font-size: 1.25rem;
  background-color: rgba(46, 173, 224);
}
.btn-menu:hover {
  background-color: rgb(32, 111, 142);
}

.btn-mode {
  padding: 1rem 0.2rem;
  border-color: rgba(8, 8, 8, 0.527);
  background-color: rgba(16, 114, 184);
}
.btn-mode:hover {
  background-color: rgb(9, 64, 104);
}
.btn-preset {
  padding: 0.6rem 0.7rem;
  background-color: rgba(0, 133, 67);
}
.btn-preset:hover {
  background-color: rgba(3, 92, 47);
}
.btn-silence {
  background-color: rgba(130, 130, 132);
}
.btn-silence:hover {
  background-color: rgba(104, 104, 105);
}
.flex-between {
  display: flex;
  justify-content: space-between;
  /*border: 0.5rem solid rgba(153, 209, 243, 0.421);*/
}
.flex-center {
  display: flex;
  justify-content: center;
}
.flex-col {
  margin: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.governor {
  border-radius: 1rem;
  background-color: gray;
}
.gov-bottom-contain {
  display: flex;
  border-radius: 0 0 1rem 1rem;
  background-color: rgba(0, 0, 0);
}
.gov-left-contain {
  padding: 1rem 5rem 2rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-items: space-evenly;
  border-radius: 1rem 0 0 0;
  background-color: rgba(210, 211, 213);

  /*border: solid white 0.5rem;*/
}
.gov-right-contain {
  padding: 4rem 2rem 0 1.5rem;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-items: space-evenly;
  /*border: solid rgba(255, 255, 255, 0.499) 0.5rem;*/
}
.gov-top-contain {
  display: flex;
  justify-content: space-evenly;
}
.intake-discharge {
  width: 14rem;
  padding: 0 1rem;
  font-family: "digital-7";
  font-size: 5rem;
  line-height: 5rem;
  font-weight: 500;
  text-align: right;
  border: solid rgba(0, 0, 0) 0.5rem;
  border-radius: 1rem;
  background-color: rgba(68, 14, 14);
  color: red;
}
.intake-discharge-box {
  margin: 0.5rem 0;
}
.intake-dis-figcap {
  font-size: 1.2rem;
  line-height: 1.5rem;
  font-weight: 800;
  text-align: center;
}
.intake-dis-override {
  font-size: 3rem;
  text-align: center;
}
.light-checkStop {
  background-color: rgba(86, 45, 26);
  border: 0.3rem solid rgba(146, 58, 55);
}
.light-contain-H {
  margin: 0.5rem 0;
  display: flex;
  align-items: center;
  font-size: 0.75rem;
  font-weight: 800;
  /*border: 2px solid rgba(128, 128, 128, 0.814);*/
}
.light-contain-V {
  margin: 0.5rem 0.9rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 0.7rem;
  font-weight: 800;
}
.light-check-stop {
  color: rgba(189, 187, 94);
}
.light-pressure {
  background-color: rgba(255, 255, 0);
  border: 0.3rem solid rgba(118, 103, 3);
}
.light-rnd {
  display: inline;
  /*font-size: 5px;*/
  width: 1.5rem;
  height: 1.5rem;
  margin: 0.1rem;
  border-radius: 50%;
}
.light-rpm {
  background-color: rgba(77, 1, 1);
  border: 0.3rem solid rgba(41, 2, 2);
}
.light-throttle {
  background-color: rgba(5, 215, 5);
  border: 0.3rem solid rgba(1, 118, 1);
}
.rpm {
  text-align: center;
  font-size: 1.5rem;
  font-weight: 400;
  color: white;
}
.status-icons {
  width: 1.75rem;
  filter: invert(100%);
}
.status-lights-contain {
  margin: 0 0 0 2rem;
}
.status-lights-label {
  font-size: 0.7rem;
  color: white;
}
.sub-right {
  margin: 0 0 5rem 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  /*border: 5px solid yellow;*/
}
.total-control {
  font-size: 2rem;
  font-style: italic;
  font-weight: 900;
}
.total-control-red {
  margin: 0 0 0 0.6rem;
  padding: 0.5rem;
  border: 0.25rem solid black;
  border-radius: 50%;
  font-size: 1.5rem;
  font-style: none;
  font-weight: 900;
  color: red;
  border-width: 0 0 0.5rem 0.9rem;
}
.TEMP-status {
  height: 5rem;
  width: 0.7rem;
  margin: 3rem 0 0.2rem 0;
  background-color: rgb(1, 104, 1);
  border-right: 3px dashed black;
}

/************* TEMP ************/
h1,
h3 {
  margin: 0.5rem 1rem 4rem 1rem;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 500;
  letter-spacing: 0.1rem;
  text-align: center;
  color: white;
}
h3 {
  margin: 7rem 1rem 1rem 1rem;
  font-size: 2rem;
}

.govImg {
  margin: 1rem 1rem 3rem 1rem;
  max-width: 20rem;
}
